<template>
	<Modal_body :close="false" title="Chybn hodnota vo formulri." class="modal-error" >

		<p>
			Pri prepote produktov sme objavili nesprvnu hodnotu. <br>
			Vrte sa prosm a upravte ju.
		</p>

		<p v-for="(value, id) in errors" >
			<strong>{{ id }}</strong> :
			<span v-for="(error, key) in value" >
				{{ error }}
				<br>
			</span>
		</p>

		<button class="btn btn-primary btn-left mt-2 mb-4" @click="go_back">
			Vrti sa na predchdzajci krok
		</button>

		<p>
			token: {{ token }}
			<br>
			time: {{ time }}
		</p>

		<p>
			Pre bliie info o alom postupe ns prosm kontaktuje: <br>
			<a :href="'mailto:' + mail">{{ mail }}</a>
			<br>
			<span v-if="phone">{{ phone }}</span>
		</p>

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_core from '@/store/STATE_core.js';
	import Constants from '@/logic/Constants.js';
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
		},
		components: {
			Modal_body,
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			go_back()
			{
				STATE_core().change_step(1);
			},
		},
		computed: {
			errors()
			{
				return STATE_modals().retrieve_modal_data() || false;
			},
			token()
			{
				 return STATE_core().get_session_token();
			},
			time()
			{
				return new Date().toLocaleString();
			},
			mail()
			{
				return Constants.email_address();
			},
			phone()
			{
				if (STATE_params().direct_user_no_agent()) {
					return null;
				}
				return Constants.phone_number(true);
			},
		},
		watch: {
		},
	}
</script>
