import STATE_core from '@/store/STATE_core.js';

/**
 * Constants
*/
class Constants
{
	/**
	 * email_address
	*/
	email_address()
	{
		if (STATE_core().is_partners_skin()) {
			return 'poistenie@partnersgroup.sk';
		}
		return 'najpoistenie@najpoistenie.sk';
	}


	/**
	 * phone_number
	*/
	phone_number(tech_support)
	{
		if (tech_support) {
			// technical support for agents
			return '+421 905 497 554';
		}
		// common public users
		return '+421 917 535 773';
	}


	/**
	 * group_names
	*/
	group_names()
	{
		return {
			1: 'Povinn zmluvn poistenie',
			2: 'Havarijn poistenie',
			3: 'Hypotekrny ver',
			4: 'ivotn poistenie',
			5: 'GAP poistenie finannej straty',
			6: 'Investcie',
			7: 'Cestovn poistenie',
			8: 'Poistenie zodpovednosti zamestnanca',
			9: 'Poistenie majetku - byt, dom, domcnos',
			10: 'Poistenie podnikateov',
			72: 'Sedadl',
			73: 'Zdravotn poistenie cudzincov',
			82: 'Test',
			85: 'Starobn dchodkov sporenie', // dss
			86: 'Doplnkov dchodkov sporenie', // dds
		};
	}
}

export default new Constants;
